input {
  height: 45px;
  margin-bottom: 28px;
  margin-top: 16px;
  display: inline-block;
  width: 72px;
  border-radius: 8px;
  border: 1px solid #dfdee0;
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  color: #21092f;
  padding: 10px;
}
input:focus {
  outline: none;
}
input::placeholder {
  padding: 10px;

  font-size: 18px;
  line-height: 23px;
}
.info {
  width: 327px;
  display: block;
}
.form-container {
  display: flex;
  justify-content: center;
}
form {
  display: flex;
  flex-direction: column;
  align-items: left;
}
.date,
.labels {
  width: 327px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.cvc-input {
  width: 164px;
}
label {
  text-align: left;
}
button {
  width: 327px;
  height: 53px;
  background-color: #21092f;
  color: white;
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  border-radius: 8px;
  cursor: pointer;
}
