header {
  background-image: url("../images/bg-main-mobile.png");
  background-size: cover;
  width: 100%;
  height: 240px;
  display: flex;
  justify-content: center;
  padding-top: 32px;
  font-family: "Space Grotesk";
  font-style: normal;
  font-weight: 500;
  margin-bottom: 100px;
}
.cards {
  width: 342px;
  height: 100%;
  position: relative;
}
.card-back,
.card-front {
  background-size: cover;
  width: 286px;
  height: 157px;
  position: absolute;
  font-size: 9px;
  line-height: 11px;

  letter-spacing: 1.28571px;
  color: white;
}
.card-back {
  background-image: url("../images/bg-card-back.png");
  left: 56px;
  z-index: 0;
  text-align: right;
}
.card-front {
  background-image: url("../images/bg-card-front.png");
  z-index: 1;
  top: 92px;
  padding: 18px;
  text-align: left;
}
.card-front img {
  height: 31px;
  margin-right: 212px;
  margin-bottom: 32px;
}
.cvc {
  position: absolute;
  top: 72px;
  right: 37px;
}
h2 {
  font-size: 18px;
  line-height: 23px;
  letter-spacing: 2.2px;
  font-weight: 500;
  margin-bottom: 17px;
}
.card-holder {
  display: flex;
  justify-content: space-between;
}
.expire {
  text-align: right;
}

@media screen and (min-width: 1050px) {
  header {
    background-image: url("../images/bg-main-desktop.png");
    width: 33.5417vw;
    min-height: 100vh;
    margin-bottom: 0;
  }
  .card-back,
  .card-front {
    background-size: cover;
    width: 400px;
    height: 220px;
    position: absolute;
    font-size: 14px;
    line-height: 18px;
    text-align: right;
    letter-spacing: 2px;
    color: white;
  }
  .card-back {
    top: 52.1vh;
    left: 11.9167vw;
  }
  .card-front {
    top: 11.1806vh;
    left: 7vw;
  }
  .card-front img {
    margin-right: 300px;
  }
  h2 {
    font-size: 1.6vw;
    line-height: 2.3vw;
    letter-spacing: 3.42222px;
    text-align: left;
  }
  .cvc {
    top: 96px;
  }
}
