.App {
  text-align: center;
  width: 100vw;
}

h1 {
  font-size: 28px;
  line-height: 36px;
  margin-top: 35px;
  margin-bottom: 16px;
  text-align: center;
  letter-spacing: 3.42222px;
  color: #21092f;
}
h3 {
  font-size: 18px;
  line-height: 23px;
  text-align: center;
  color: #8f8694;
  margin-bottom: 48px;
  font-weight: 500;
}

@media screen and (min-width: 1050px) {
  main {
    display: flex;
  }
  .container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
